import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { addDraft } from '../../actions/draft';
import { getCurrentProfile } from '../../actions/profile';
import Upload from '../uploader/Upload';
import api from "../../utils/api";

const DraftTypeChoice = ({ addDraft,
                         }) => {
  const [text, setText] = useState('');

    useEffect(() => {
        getCurrentProfile();
    }, [getCurrentProfile]);

  const uploadFinished = () => {
      let start = new Date().getTime();

      const waitProc = () => {
          setTimeout(async () => {
              const response = await api.get('/profile/uploading')
              let end = new Date().getTime()
              console.log('check_uploading::response.data.type ', typeof (response.data))
              console.log('check_uploading::response.data ', response.data)

              if (response.data === 1 || (end - start) > 60 * 1000) {
                  console.log('equal::', response.data)
                  start = 0
              } else {
                  waitProc()
              }
          }, 1000)
      }
      waitProc()
  }
  return (
    <div className='draft-form'>
      <div className='bg-primary p'>
        <h3>Create a Draft...</h3>
      </div>
        <div className="Card">
          <Upload uploadFinished={uploadFinished}/>
        </div>
      <form
        className='form my-1'
        onSubmit={e => {
          e.preventDefault();
          addDraft({ text });
          setText('');
        }}
      >
        <textarea
          name='text'
          cols='30'
          rows='5'
          placeholder='Create a draft'
          value={text}
          onChange={e => setText(e.target.value)}
          required
        />
        <input type='submit' className='btn btn-dark my-1' value='Submit' />
      </form>
    </div>
  );
};

DraftTypeChoice.propTypes = {
  addDraft: PropTypes.func.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
    auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
    profile: state.profile,
    auth: state.auth,
});
export default connect(
    mapStateToProps,
  { addDraft, getCurrentProfile }
)(DraftTypeChoice);



